import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter,
} from '@angular/core';
import {
  Booking,
  selectActiveForm,
  storeBookingFormData,
  BookingFormData,
  checkCollisions,
  getStatusErrorMessage,
  clearBookingFormData,
  BOOKING_STATUS
} from '@mckinsey-converge/data-common';
import {
  select,
} from '@ngrx/store';
import {
  distinctUntilChanged,
} from 'rxjs/operators';
import { BookingFormBaseComponent } from './booking-form-base.component';

@Component({
  selector: 'app-booking-edit',
  templateUrl: './booking-form-base.component.html',
  styleUrls: ['./booking-create.component.scss']
})
export class BookingEditComponent extends BookingFormBaseComponent implements OnInit {
  @Input() public booking: Booking;
  @Output() public updateCallback = new EventEmitter<Booking>();
  public roomEmail: string;

  public ngOnInit(): void {
    super.ngOnInit();
    this.subscription('activeForm', this.store.pipe(
      select(selectActiveForm),
      distinctUntilChanged()
    ).subscribe((next: BookingFormData | undefined) => {
      if (next) {
        this.data.next(next);
      }
    }));

    this.modal = true;
    this.roomEmail = this.booking.space.email;
    const attendees = this.booking.attendees.map(u => {
      return {
        value: u.email,
        display: u.name,
        groupId: 'one',
        groupHeader: true,
        shortDisplay: u.name,
        meta: { external: u.external, user: u, color: u.email.toLowerCase().includes('mckinsey.') ? 'internal' : 'external' },
      }
    });

    const company = this.booking.company.map(u => {
      return {
        value: u,
        display: u,
        groupId: 'two',
        groupHeader: true,
        shortDisplay: u,
      }
    });

    const head_count = this.booking?.expected_attendees[this.roomEmail] ? this.booking?.expected_attendees[this.roomEmail] : 0;
    const code = this.booking?.equipment_codes[this.roomEmail] ? this.booking?.equipment_codes[this.roomEmail] : '';
    const organiser = {
      display: this.booking.organiser.name,
      value: this.booking.organiser.email,
      meta: { user: this.booking.organiser }
    };
    const notes = this.booking.notes.find(n => n.type === 'equipment')?.message;
    const selectedRoom = {
      space: this.booking.room, // this.building,
    };

    this.store.dispatch(storeBookingFormData({
      payload: {
        ...this.booking.toJSON(),
        attendees,
        code,
        head_count,
        notes,
        organiser,
        selectedRoom,
        company
      }
    }));
  }

  public updateNotes(booking, note, room) {
    const notes = [];
    const cateringNotes = this._cateringNotesService.getNotes();
    const isEquipmentNote = booking.notes.find(note => note.type === 'equipment');
    const isCateringNote = booking.notes.find(note => note.type === 'catering');

    if (cateringNotes.length && !isCateringNote) {
      notes.push(cateringNotes[0]);
    }

    if (isEquipmentNote) {
      booking.notes.map(item => {
        const message = cateringNotes.length && item.type === 'catering' ? cateringNotes[0].message : item.message;

        notes.push({ 
          ...item, 
          author : this.currentUser.name,
          message : item.type === 'equipment' ? note : message,
          space: item.type === 'equipment' ? room : item.space,
          date: item.type === 'equipment' ? new Date().getTime() : item.date
        })
      });
    } else {
      if (note) {
        notes.push({
          author: this.currentUser.name,
          type: "equipment",
          message: note,
          space: room,
          date: new Date().getTime()
        })
      }
    }
    return notes;
  }

  /**
   * Save booking changes
   */
  public saveChanges() {
    const booking_form_data = this.data.value;
    // clone booking to allow editing
    const booking = this.booking.clone();

    booking.title = booking_form_data.title;
    booking.attendees = this.formToAttendees(booking_form_data.attendees);
    booking.company = this.formToCompany(booking_form_data.company);
    booking.notes = this.updateNotes(this.booking, booking_form_data.notes, this.roomEmail);;
    booking.equipment_codes = { [this.roomEmail]: booking_form_data.code };
    booking.expected_attendees = { [this.roomEmail]: booking_form_data.head_count };
    booking.organiser = booking_form_data.organiser?.meta.user;
    const has_note_buffer = booking_form_data.notes && booking_form_data.notes?.length ? {[booking_form_data.selectedRoom?.space?.email || booking?.space?.email]: 900} : null; // 900 seconds = 15 min
    // Removing the note does not remove the existing setup / breakdown
    if (has_note_buffer) {
      booking.setup = has_note_buffer;
      booking.breakdown = has_note_buffer;      
    }
    this.booking = booking;
    this.loading = true;
    checkCollisions(
      this._spaces,
      this.booking
    ).then((bookable: boolean) => {
        // We expect the current booking to be returned form this api call, 
        // but shouldn't see anything else, except the API is retuning bookings ouside of requested time range.
        // TODO - Refactor for https://acaprojects.atlassian.net/browse/MCK-1498
        if (bookable) {
          this._bookings_service.save(this.booking).then(
            (bkn) => {
              this.loading = false;
              this.success = true;
              this.updated = true;
              this.confirmed = true;
              this.updateCallback.emit(this.booking);
              this._booking_state_service.replace(this.booking);
              // Clear the current booking form
              this.store.dispatch(clearBookingFormData());
              // Heap track event
              this._service.analytics.track('update', {
                booking_id: booking.id,
                office: booking.room.building.name, // this.building.name,
                room: booking.room.name,
                start_time: booking.startDateTz.ms,
                end_time: booking.endDateTz.ms,
                catering: !!booking.catering.length,
                headcount: booking_form_data.head_count,
              });
            },
            (err) => {
              this.loading = false;
              this.confirmed = false;
              if (err.status === 409) {
                this.booking.status = BOOKING_STATUS.unavailable
                this._booking_state_service.replace(this.booking);
                this.confirmed = true;
                this.success = false;
              } else {
                  this.displayWarningBanner(getStatusErrorMessage(err.status));
              }
            }
          );
        } else {
          this.loading = false;
          this.confirmed = false;
          this.booking.status = BOOKING_STATUS.unavailable
          this._booking_state_service.replace(this.booking);
          this.confirmed = true;
          this.success = false;
          return;
        }
    },
      (err) => {
        this.loading = false;
        this.confirmed = false;
        this.booking.status = BOOKING_STATUS.unavailable
        this._booking_state_service.replace(this.booking);
        this.confirmed = true;
        this.success = false;
        
      }
    );
  }

  public callback() {
    this.closeCallback.emit(true);
  }
}
