import { SpaceFilterReason } from '@mckinsey-converge/data-common';

export interface BookingRulesLink {
    name: string;
    url: string;
}
export class RoomResultsCoachmarkViewmodel {
    public visible: boolean;
    public title: string;
    public actionButtonText: string;

    constructor(public reason: SpaceFilterReason, public rules_link?: BookingRulesLink) {
        this.visible = true;
        switch (reason) {
            case SpaceFilterReason.DateTime:
            case SpaceFilterReason.OfficeRules:
                this.title =
                    'There are no available rooms that match your search criteria. ' +
                    'Please adjust your search criteria and try again.';
                if (rules_link) {
                    this.title +=
                        `<br/>For information on booking rules please see ` +
                        `<a href="${rules_link.url}" target="_blank" rel="noopener noreferer">${rules_link.name}</a>`;
                }
                this.actionButtonText = 'Return to search';
                break;
            case SpaceFilterReason.Filters:
                this.title =
                    'No rooms available for your filter criteria. ' +
                    'Try to clear the filter please.';
                this.actionButtonText = 'Clear filter';
                break;
            case SpaceFilterReason.None:
                this.visible = false;
                this.title = '';
                this.actionButtonText = '';
                break;
        }
    }
}
