import {
    Component,
    EventEmitter,
    OnInit,
    Output,
    ViewEncapsulation
} from '@angular/core';
@Component({
    selector: 'app-map-controls',
    template: `
        <div class="mck-column map-controls-container">

            <div class="mck-column map-controls-row">
                <button
                    name="zoom-in-button"
                    mat-stroked-button
                    (click)="zoomInClick.emit()"
                    class="mck-stroked-button mck-square mck-thin-border-light">
                    <div class="map-icon-zoom-in" aria-label="Zoom In"></div>
                </button>
                <button
                    name="zoom-out-button"
                    mat-stroked-button
                    (click)="zoomOutClick.emit()"
                    class="mck-stroked-button mck-square mck-thin-border-light">
                    <div class="map-icon-zoom-out" aria-label="Zoom Out"></div>
                </button>
                <button
                    name="refresh-button"
                    mat-stroked-button
                    (click)="refreshClick.emit()"
                    class="mck-stroked-button mck-square mck-thin-border-light">
                    <div class="map-icon-refresh" aria-label="Reset View"></div>
                </button>
            </div>
        </div>
    `,
    styleUrls: ['./map-controls.component.scss'],
    encapsulation: ViewEncapsulation.Emulated
})
export class MapControlsComponent implements OnInit {

    @Output()
    public zoomOutClick = new EventEmitter();

    @Output()
    public zoomInClick = new EventEmitter();

    @Output()
    public refreshClick = new EventEmitter();

    @Output()
    public infoClick = new EventEmitter();

    constructor() {
    }

    ngOnInit(): void {
    }

}
