import {
    ChangeDetectionStrategy,
    ChangeDetectorRef,
    Component,
    EventEmitter,
    Input,
    OnInit,
    Output,
    ViewContainerRef
} from '@angular/core';
import {
    select,
    Store
} from '@ngrx/store';
import {
    ActivatedRoute,
    Router
} from '@angular/router';
import {
    BehaviorSubject,
    combineLatest,
    Observable
} from 'rxjs';
import { 
    RoomResultsViewModel,
    BookingFormData,
    BookingStoreState,
    selectActiveForm,
    storeBookingFormData,
    SpaceFilterReason,
    User,
    OrganisationService,
} from '@mckinsey-converge/data-common';
import {
    map,
    take,
    distinctUntilChanged
} from 'rxjs/operators';
import {
    AlertBannerDisplay,
    AlertMode,
    BaseTemplateComponent
} from '@mckinsey-converge/ui';
import { RoomResultsCoachmarkViewmodel } from 'src/app/booking/room-results/room-results-coachmark.viewmodel';
import {
    RoomStoreState
} from '@mckinsey-converge/rooms';
import {
    BuildingStoreState,
} from '@mckinsey-converge/buildings';
import { selectCurrentUser, UserStoreState } from '@mckinsey-converge/user';


export type RoomResultsState = BookingStoreState
    & RoomStoreState
    & BuildingStoreState
    & UserStoreState;

@Component({
    selector: 'app-room-results-paginated',
    template: `
        <ng-template>
            <div class="mck-row mck-center">
                <div class="mck-content-body mck-column mck-content-expand expanded-content">
                    <ng-container *ngFor="let c of [coachMark | async]">
                        <ng-container *ngIf="(isLoading | async) === false && c && c.visible">
                            <mck-alert-banner
                                [display]="AlertBannerDisplay.V2"
                                [showClose]="false"
                                [mode]="AlertMode.BlueCoachmark"
                                [message]="c.title"
                                [buttonText]="c.actionButtonText"
                                (action)="coachMarkAction.next(c.reason)"
                            >
                            </mck-alert-banner>
                            <mck-informational-image
                                src="assets/img/booking-coachmark-no-results.svg"></mck-informational-image>
                        </ng-container>
                    </ng-container>
                    <ng-container *ngIf="!isLoading || (isLoading | async) === false && (displayViewModels | async)?.length" >
                        <app-room-result-item
                            *ngFor="let result of (displayViewModels | async); trackBy: trackViewModelById"
                            [viewModel]="result"
                            [data]="data | async"
                            [user]="currentUser"
                            (roomSelect)="roomSelected($event)"></app-room-result-item>

                        <div class="mck-column mck-center">
                            <button mat-button color="primary"
                                    class="mck-primary-button load-more-button"
                                    (click)="loadMore()"
                                    *ngIf="showLoadMore | async">
                                Load more ({{remainingResults | async}})
                            </button>
                        </div>
                    </ng-container>
                    <ng-container *ngIf="(isLoading && (isLoading | async) === true) && !(displayViewModels | async)?.length" >
                        <app-room-result-item *ngFor="let i of [0, 1, 2]">
                        </app-room-result-item>
                        <div class="mck-column mck-center">
                            <ngx-skeleton-loader class="mck-skeleton-load-more"></ngx-skeleton-loader>
                        </div>
                    </ng-container>
                </div>
            </div>
        </ng-template>
    `,
    styleUrls: ['./room-results-paginated.component.scss'],
    changeDetection: ChangeDetectionStrategy.OnPush
})
export class RoomResultsPaginatedComponent extends BaseTemplateComponent implements OnInit {
    public ROOM_DISPLAY_STEP = 10;
    public ROOM_DISPLAY_START = 3;
    public AlertMode = AlertMode;
    public AlertBannerDisplay = AlertBannerDisplay;
    /** Offset to add to the start time when selecting a space */
    @Input() public offset = 0;

    @Input()
    public viewModels: Observable<RoomResultsViewModel[] | undefined>;

    @Input()
    public coachMark: Observable<RoomResultsCoachmarkViewmodel> | undefined;

    /**
     * If true, show skeleton.
     */
    @Input()
    public isLoading: Observable<boolean>;

    @Output()
    public coachMarkAction = new EventEmitter<SpaceFilterReason>();

    public displayCount = new BehaviorSubject(this.ROOM_DISPLAY_START);

    /**
     * Fake pagination since API does not support it.
     */
    public displayViewModels: Observable<RoomResultsViewModel[] | undefined>;

    public showLoadMore: Observable<boolean>;

    public remainingResults: Observable<number>;

    public data = new BehaviorSubject<BookingFormData | undefined>(undefined);
    public currentUser: User;

    constructor(vcRef: ViewContainerRef,
        private store: Store<RoomResultsState>,
        private router: Router,
        private route: ActivatedRoute,
        private _org: OrganisationService,
        private cdRef: ChangeDetectorRef) {
        super(vcRef);
    }

    public ngOnInit(): void {
        super.ngOnInit();
        //test commet for to trigger build again
        this.displayViewModels = combineLatest([this.viewModels, this.displayCount])
            .pipe(
                map(([vm, displayCount]) => { this.cdRef.markForCheck(); return vm?.slice(0, displayCount) } ));

        this.showLoadMore = combineLatest([this.viewModels, this.displayCount])
            .pipe(
                map(([vm, displayCount]) => {
                    return ((vm?.length ?? 0) > displayCount);
                })
            );
        this.remainingResults = combineLatest([this.viewModels, this.displayCount])
            .pipe(
                map(([vm, displayCount]) => vm && (vm.length - displayCount) || 0));
        
        this.subscription('currentUser', this.store.select(selectCurrentUser)
            .pipe(take(1))
            .subscribe((user: User | undefined) => {
                // Once we know we have the user loaded.
                this.currentUser = new User(user);
            })
        );

        this.subscription('activeForm', this.store.pipe(
            select(selectActiveForm),
            distinctUntilChanged()
        ).subscribe((next: BookingFormData | undefined) => {
            if (next) {
                this.data.next(next);
                this.cdRef.detectChanges();

            }
        }));
        this.cdRef.detectChanges();
    }

    public loadMore() {
        // fake pagination
        this.displayCount.pipe(
            take(1),
            map(v => v + this.ROOM_DISPLAY_STEP)
        ).subscribe(next => {
            this.displayCount.next(next);
            this.cdRef.detectChanges();
        });
    }

    public trackViewModelById(index: number, item: RoomResultsViewModel) {
        return item.space.id;
    }

    public roomSelected(room: RoomResultsViewModel) {
        let { dateTz } = this.data.value;
        if (this.offset) {
            dateTz = dateTz.addValue({ minutes: this.offset });
        }
        /**
         * selectedRoom.space is fully fledged Space class
         * this.data.value.booking.room is not and does not contain the timezone
         * this.data.value.booking.timezone is legit 
         */ 
        // UPDATE: providing payload as part of extras instead dispatching storeBookingFormData event to avoid redundant backend request calls 
        this.router.navigate(['room'],  { relativeTo: this.route, state:{payload : { 
					...this.data.value,
                    buildings: this._org.buildings, // require to manage browser refresh on room detail screen!
					dateTz,
					offset: this.offset,
					selectedRoom: room, }}});
    }
}
