import {
    Component,
    EventEmitter,
    Input,
    OnInit,
    Output,
    SimpleChanges,
    ViewEncapsulation,
} from '@angular/core';
import { BehaviorSubject, combineLatest, Observable, of, Subject } from 'rxjs';
import { LoadingModel } from '@mckinsey-converge/loading';
import { map } from 'rxjs/operators';
import { BookingRulesLink, RoomResultsCoachmarkViewmodel } from 'src/app/booking/room-results/room-results-coachmark.viewmodel';
import {
    Building,
    BuildingLevel,
    mapResultsToViewModels,
    RoomResultsViewModel,
    FilteredSpaces,
    SpaceFilterReason,
} from '@mckinsey-converge/data-common';
import { BaseDirective } from '@mckinsey-converge/base';

@Component({
    selector: 'app-room-results-section',
    template: `
        <div *ngIf="!collapsable" class="mck-content-expand-width main-results-container">
            <ng-container *ngTemplateOutlet="pagination"></ng-container>
        </div>

        <mck-collapse-header
            *ngIf="collapsable"
            [isLoading]="resultsLoading | async"
            title="{{collapseTitle}}"
            description="{{collapseDescription}}"
        >
            <ng-container [ngTemplateOutlet]="pagination"></ng-container>
        </mck-collapse-header>

        <ng-template #pagination>
            <app-room-results-paginated
                [isLoading]="resultsLoading"
                [viewModels]="mainResultViewModels"
                [coachMark]="coachMarkDisplay"
                [offset]="offset"
                (coachMarkAction)="onCoachmarkAction($event)">
            </app-room-results-paginated>
        </ng-template>
    `,
    styleUrls: ['./room-results-section.component.scss'],
    encapsulation: ViewEncapsulation.Emulated
})
export class RoomResultsSectionComponent extends BaseDirective implements OnInit {

    @Input() public rules_link?: BookingRulesLink;
    /** Offset to add to the start time when selecting a space */
    @Input() public offset = 0;

    @Input()
    public collapsable: boolean;

    /**
     * If collapsable is true, this is used in the collapse header.
     */
    @Input()
    public collapseTitle: string;

    /**
     * If collapsable is true, this is used in the collapse header.
     */
    @Input()
    public collapseDescription: string;

    @Input()
    public levelBuildings: Observable<{ flattenedLevels: BuildingLevel[]; buildings: Building[] }>;

    @Input()
    public results: BehaviorSubject<LoadingModel<FilteredSpaces>>;

    @Output()
    public coachMarkAction = new EventEmitter<SpaceFilterReason>();

    public mainResultViewModels = new BehaviorSubject<RoomResultsViewModel[] | undefined>(
        undefined
    );

    public resultsLoading: Observable<boolean>;
    public coachMarkDisplay: Observable<RoomResultsCoachmarkViewmodel>;

    private subscribeResultsToViewModels = (
        subName: string,
        resultsObservable: Observable<LoadingModel<FilteredSpaces>>,
        flattenLevelBuildings: Observable<{ flattenedLevels: any; buildings: Building[] }>,
        viewModelSubject: Subject<RoomResultsViewModel[]>
    ) => {
        this.subscription(
            subName,
            combineLatest([
                resultsObservable.pipe(map((r) => r.optionalSuccess?.spaces || [])),
                flattenLevelBuildings,
            ])
                .pipe(
                    map(([results, { flattenedLevels, buildings }]) =>
                         mapResultsToViewModels( (results?.length && results[0]?.building) ? results : [], flattenedLevels, buildings)
                    )
                )
                .subscribe((next) => {
                    viewModelSubject.next(next);
                })
        );
    };

    public ngOnInit(): void {
        this.resultsLoading = this.results.pipe(map((m) => m.isLoading));
        this.coachMarkDisplay = this.results.pipe(
            map((r) => r.optionalSuccess?.reason || SpaceFilterReason.None),
            map((r) => new RoomResultsCoachmarkViewmodel(r, this.rules_link))
        );

        this.subscribeResultsToViewModels(
            'resultListing',
            this.results,
            this.levelBuildings || of({ flattenedLevels: [], buildings: [] }),
            this.mainResultViewModels
        );
    }

    public ngOnChanges(changes: SimpleChanges) {
        if (changes.results || changes.levelBuildings || changes.mainResultViewModels) {
            this.subscribeResultsToViewModels(
                'resultListing',
                this.results,
                this.levelBuildings || of({ flattenedLevels: [], buildings: [] }),
                this.mainResultViewModels
            );
        }
    }

    public onCoachmarkAction(reason: SpaceFilterReason) {
        this.coachMarkAction.emit(reason);
    }
}
