import { LoadingModel } from '../../../loading/src/lib/loading.model';
import {
    ISpaceAvailabilityOptions,
    Space
} from '@mckinsey-converge/data-common';
import { DateTZ } from '@mckinsey-converge/date-tz';

export interface RoomStoreState {
    rooms: RoomState;
}

export interface RoomState {
    query: LoadingModel<Space[]>
    later: LoadingModel<Space[]>
    next: LoadingModel<Space[]>
    byid: LoadingModel<Space[]>
    size: LoadingModel<Space[]>
}

export interface RoomQueryOptions {
    dateTz: DateTZ;
    duration: number;
    locations: string;
    capacity?: number;
    capacity_max?: number;
    diff_capacity?: boolean;
}

export const roomQueryOptionsToSpaceAvailabilityOptions = (options: RoomQueryOptions): ISpaceAvailabilityOptions => ({
    dateTz: options.dateTz,
    duration: options.duration,
    capacity: options.capacity,
    zone_ids: options.locations,
    capacity_max: options.capacity_max,
    diff_capacity: options.diff_capacity
});

export const roomByIdQueryOptionsToSpaceAvailabilityOptions = (options: RoomQueryOptions): ISpaceAvailabilityOptions => ({
    dateTz: options.dateTz,
    duration: options.duration,
    capacity: options.capacity,
    room_ids: options.locations,
    capacity_max: options.capacity_max
});
