import {
    Component,
    OnInit,
} from '@angular/core';
import {
    select,
} from '@ngrx/store';
import {
    Booking,
    BookingFormData,
    selectActiveForm,
    checkCollisions,
    getStatusErrorMessage,
    clearBookingFormData,
    BOOKING_STATUS
} from '@mckinsey-converge/data-common';
import {
    distinctUntilChanged,
} from 'rxjs/operators';
import { BookingFormBaseComponent } from './booking-form-base.component';

@Component({
    selector: 'app-booking-create',
    templateUrl: './booking-form-base.component.html',
    styleUrls: ['./booking-create.component.scss']
})
export class BookingCreateComponent extends BookingFormBaseComponent implements OnInit {
    public ngOnInit(): void {
        super.ngOnInit();
        this.subscription('activeForm', this.store.pipe(
            select(selectActiveForm),
            distinctUntilChanged()
        ).subscribe((next: BookingFormData | undefined) => {
            if (next) {
                this.data.next(next);
                this.booking = this.bookingFormToBooking();
            }
        }));
    }

    public bookingFormToBooking(): Booking {
        let booking_form_data = this.data.value;
        const has_note_buffer = booking_form_data.notes && booking_form_data.notes?.length ? {[booking_form_data.selectedRoom.space.email]: 900} : {}; // 900 seconds = 15 min
        const attendees = this.formToAttendees(booking_form_data.attendees);
        const company = this.formToCompany(booking_form_data.company);
        const start_epoch = booking_form_data.dateTz.seconds;
        const end_epoch = booking_form_data.dateTz.addValue({ minutes: booking_form_data.duration }).seconds;

        const raw_data = {
            all_day: false,
            attendees,
            booking_type: booking_form_data.bookingType.value,
            catering: [],
            start_epoch,
            end_epoch,
            timezone: booking_form_data.timezone,
            equipment_codes: {[booking_form_data.selectedRoom.space.email]: booking_form_data.code},
            expected_attendees: {[booking_form_data.selectedRoom.space.email]: booking_form_data.head_count},
            has_catering: false,
            needs_space: false,
            organiser: this.setupOrganiser(booking_form_data),
            notes: this.setupNotes(booking_form_data.notes, booking_form_data.selectedRoom.space.email),
            creator: booking_form_data.creator,
            room: booking_form_data.selectedRoom.space,
            title: booking_form_data.title,
            setup: has_note_buffer,
            breakdown: has_note_buffer,
            company
        };

        return new Booking(raw_data);
    }

    setupOrganiser(booking_form_data: BookingFormData) {
        if(booking_form_data.organiser?.meta?.user){
            return booking_form_data.organiser?.meta?.user;
        }
        else{
            booking_form_data.organiser = {...booking_form_data.organiser, meta: {user: this.currentUser}}
            return booking_form_data.organiser?.meta.user;
        }
    }

    /**
     * Save booking changes
     */
    public saveChanges() {
        this.loading = true;
        this.booking = this.bookingFormToBooking();
        const booking_form_data = this.data.value;
        this.booking.status = BOOKING_STATUS.approved;
        checkCollisions(
            this._spaces,
            this.booking
        ).then((bookable: boolean) => {
            if (!bookable) {
                // TODO - Refactor for https://acaprojects.atlassian.net/browse/MCK-1498
                // API is retuning bookings ouside of requested time range
                // TODO check booking date and times for conflict manually
                this.booking.status = BOOKING_STATUS.unavailable;
                return;
            }

            this._bookings_service.save(this.booking).then(
                (bkn) => {
                    this.loading = false;
                    this.success = true;
                    this.confirmed = true;
                    this._booking_state_service.replace(bkn);

                    // When saved, the returned booking has a null room.
                    // Correct this so the rest of the code doesn't flake out.
                    this.booking = new Booking({ ...bkn.toJSON(), room: this.booking.room });
                    // Clear the current booking form
                    this.store.dispatch(clearBookingFormData());

                    // Heap track event
                    this._service.analytics.track('create', {
                        booking_id: bkn.id,
                        office: this.booking.room.building.name,
                        room: this.booking.room.local_name,
                        start_time: bkn.startDateTz.ms,
                        end_time: bkn.endDateTz.ms,
                        catering: !!bkn.catering.length,
                        headcount: booking_form_data.head_count,
                    });
                },
                (err) => {
                    this.loading = false;
                    this.confirmed = false;
                    if (err.status === 409) {
                        // Unavailable Confirmation
                        this.booking.status = BOOKING_STATUS.unavailable;
                        this._booking_state_service.replace(this.booking);
                        this.confirmed = true;
                        this.success = false;
                    } else {
                        this.displayWarningBanner(getStatusErrorMessage(err.status));
                    }
                }
            );
        },(err) => {
                // Unavailable Confirmation
                this.loading = false;
                this.timeout('error', () => {
                    this.booking.status = BOOKING_STATUS.unavailable;
                    this._booking_state_service.replace(this.booking);
                    this.confirmed = true;
                    this.success = false;
                }, 500)
            }
        );
    }
}
