import {
    Component,
    Inject,
    ViewEncapsulation
} from '@angular/core';
import {
    dateBuildingFormatString,
    startEndTimeWithZoneFormatString,
} from '@mckinsey-converge/base';
import { Booking, Building } from '@mckinsey-converge/data-common';
import {
    MAT_DIALOG_DATA,
    MatDialogRef
} from '@angular/material/dialog';
import { BaseClass }  from '@mckinsey-converge/base';

interface data {
    booking: Booking;
    building: Building;
    callback: Function;
}

@Component({
    selector: 'app-cancel-booking-modal',
    template: `
        <mat-dialog-content class="cancel-dialog-content">
            <div class="button-row" *ngIf="!canceled">
                <button name="CloseButton" mat-icon-button (click)="closeModal()">
                    <div class="close-image"></div>
                </button>
            </div>
            <div class="mck-column modal-heading" [class.canceled]="canceled">
                <img src="assets/icon/stop.svg">
                <div class="info">{{subtitle}}</div>
            </div>
            <div class="mck-column modal-body" *ngIf="!canceled">
                <p>{{data.booking.title}}</p>
                <p>{{dateString}}</p>
                <p>{{timeString}}</p>
                <p>{{buildingAddress}}</p>
                <p>{{data.booking.room.local_name}}</p>
            </div>
            <div class="mck-column modal-body-footer" *ngIf="!canceled">
                <mck-modal-button-row 
                    [disableFixed]="true"
                    primaryLabel="Yes" 
                    (primarySubmit)="cancelBooking()"
                    secondaryLabel="No" 
                    (secondarySubmit)="closeModal()"
                ></mck-modal-button-row>
            </div>
        </mat-dialog-content>
    `,
    styleUrls: ['./cancel-booking-modal.component.scss'],
    encapsulation: ViewEncapsulation.None,
})
export class CancelBookingModalComponent extends BaseClass {

    public title: string;
    public roomName: string;
    public buildingAddress: string;
    public dateString: string;
    public timeString: string;
    public canceled: boolean = false;

    public get subtitle() {
        return this.canceled ? `${this.title} booking cancelled` : 'Are you sure you want to cancel this booking?';
    }

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: data,
        private dialogRef: MatDialogRef<CancelBookingModalComponent>
    ) {
        super();
        this.dateString = dateBuildingFormatString(this.data.booking.startDateTz, true);
        this.timeString = startEndTimeWithZoneFormatString(this.data.booking.startDateTz, this.data.booking.displayEndDateTz);
        this.title = this.data.booking.title;
        this.buildingAddress = this.data.booking.room.street_and_city;
    }

    public closeModal() {
        this.dialogRef.close();
    }

    public cancelBooking() {
        this.canceled = true;
        this.data.callback();
        this.timeout('closeModal', () => this.closeModal(), 2000);
    }
}
