import {
  Component,
  OnInit,
  Output,
  EventEmitter,
} from '@angular/core';
import { TopBarHeaderMode, RoutePayload } from 'src/app/components/topbar-header/topbar-header.component';
import {
  Store
} from '@ngrx/store';
import {
  BaseDirective,
  dateLocalFormatString,
  startEndTimeWithZoneFormatString,
  humaniseDuration,
  toTitleCase,
  dateBuildingFormatString
} from '@mckinsey-converge/base';
import {
  ApplicationService,
  Booking,
  BookingsService,
  BookingStateService,
  Space,
  SpacesService,
  User,
  BookingNote,
  BookingFormData,
  BookingStoreState,
  storeBookingFormData,
  bookingStatusDetails,
  clearBookingFormData,
  CateringNotesService
} from '@mckinsey-converge/data-common';
import {
  BehaviorSubject,
} from 'rxjs';
import {
  take,
} from 'rxjs/operators';
import {
  BuildingStoreState
} from '@mckinsey-converge/buildings';
import {
  RoomStoreState,
} from '@mckinsey-converge/rooms';
import {
  Router
} from '@angular/router';
import { UserStoreState } from '@mckinsey-converge/user';
import { selectCurrentUser } from '@mckinsey-converge/user';

export type RoomResultsState = BookingStoreState
  & RoomStoreState
  & BuildingStoreState
  & UserStoreState;

@Component({
  selector: 'app-booking-base',
  templateUrl: './booking-form-base.component.html',
  styleUrls: ['./booking-create.component.scss']
})
export class BookingFormBaseComponent extends BaseDirective implements OnInit {
  public TopBarHeaderMode = TopBarHeaderMode;

  public backRoute: RoutePayload = {
    route: ['/home', 'results', 'room'],
    queryParams: {}
  };

  @Output() public closeCallback = new EventEmitter<boolean>();

  // Eliminate selectedRoom (room building capacity )
  public modal = false;
  public data = new BehaviorSubject<BookingFormData | undefined>(undefined);
  public submitTimestamp: number;
  public confirmed: boolean = false;
  /** Whether booking has succeeded */
  public success: boolean;
  public updated: boolean;
  /** Display loading template */
  public loading: boolean;
  public booking: Booking = new Booking({});
  /** Current User */
  public currentUser: User;
  public invalidForm: boolean = true;
  public showWarningBanner: boolean = false;
  public warningMessage: string;

  constructor(
    public _service: ApplicationService,
    public store: Store<RoomResultsState>,
    public router: Router,
    public _bookings_service: BookingsService,
    public _booking_state_service: BookingStateService,
    public _spaces: SpacesService,
    public _cateringNotesService: CateringNotesService
  ) {
    super();
  }

  public ngOnInit(): void {
    this.subscription('currentUser', this.store.select(selectCurrentUser)
      .pipe(take(1))
      .subscribe((user: User | undefined) => {
        // Once we know we have the user loaded.
        this.currentUser = new User(user);
      })
    );
  }

  public get building(){
    return this.booking.room.building;
  }

  public get type() {
    return this.booking.bookingTypeLabel;
  }

  public get internal_external_type() {
    return this.booking.room.filters.external === true? "External" : "Internal";
  }

  public get date(){
    // R--- remove if no longer used.
    return this.booking.startDateTz;
  }

  public get dateString() {
    return dateBuildingFormatString(this.booking.startDateTz);
  }

  public get timeString() {
    return startEndTimeWithZoneFormatString(this.booking.startDateTz, this.booking?.displayEndDateTz);
  }

  public get durationString() {
    return humaniseDuration(this.booking?.duration);
  }

  public get status() {
    return bookingStatusDetails(this.booking);
  }

  public async goToBackRoute() {
    await this.router.navigate(this.backRoute.route, { queryParams: this.backRoute.queryParams });
  }

  /**
   * Continue to Booking Form
   */
  public createBooking() { // todo change links based on usage
    // trigger the booking form submit
    this.submitTimestamp = new Date().getTime();
  }

  // submit the booking form
  public submitForm(formGroup: any) {
    let creator: User;
    if (formGroup.has_delegate) {
      if (formGroup.organiser.value !== this.currentUser.email) {
        creator = this.currentUser;
      }
    }

    // Combines booking with selected form fields and not the entire formGroup like other places.
    this.store.dispatch(storeBookingFormData({
      payload: {
        ...this.booking.toJSON(),
        attendees: formGroup.attendees,
        code: formGroup.code,
        head_count: formGroup.head_count,
        notes: formGroup.notes,
        organiser: formGroup.organiser,
        creator,
        title: formGroup.title,
        company: formGroup.company
      }
    }));
    this.saveChanges();
  }

  // submit the booking form
  public validateForm(valid: boolean) {
    this.invalidForm = !valid;
  }

  public setupNotes(note: string, roomEmail: string) {
    if (note && note.length) {
      // this note was required to update setup and breakdown
      return [{
        author: this.currentUser.name,
        type: "equipment",
        message: note,
        space: roomEmail,
        date: new Date().getTime()
      }] as BookingNote[];
    }

    return [];
  }

  public formToAttendees(attendees: any[]) {
    return !attendees ? [] : attendees.map(a => {
      let raw_user = {
        first_name: '',
        last_name: '',
        email: '',
        name: '',
        type: 'internal',
        external: false,
        visitor: false,
      };
      if (a.meta?.external) {
        // External users get assigned a random email
        const nameParts = a.display.split(' ');
        const length = nameParts.length;
        if (length) {
          raw_user.first_name = nameParts[0];
          raw_user.name = nameParts.join(' ');
        }
        if (length > 1) {
          nameParts.shift();
          raw_user.last_name = nameParts.join(' ');
        }
        raw_user.email = a.value ?? `${Math.floor(Math.random() * 99999)}@guest.mckinsey.com`;
        raw_user.type = 'external'; // retain external identity
        raw_user.external = true;
        raw_user.visitor = true;
      } else {
        raw_user = a.meta.user;
      }
      return new User(raw_user);
    });
  }

  public formToCompany(companies: any[]) {
    return !companies ? [] : companies.map(a => {
      return a.value;
    });
  }

  /**
   * Save booking changes
   */
  public saveChanges() {
    this.loading = true;
    const booking_form_data = this.data.value;
    // override in child class
  }

  public callback() {
    // mock interface
  }

  public get is_request(): boolean {
    const data = this.data.value;

    /* istanbul ignore else */
    if (!data?.selectedRoom?.space) {
        return false;
    }
    
    const space = new Space(data.selectedRoom.space);
    return space.byRequest({
        dateTz: data.dateTz,
        duration: data.duration,
        host: this.currentUser
    });
  }

  public displayWarningBanner(custom: string) {
    this.warningMessage = custom;
    this.showWarningBanner = true;
    this.timeout('close banner', () => {
      this.showWarningBanner = false;
    }, 10000);
  }

  public hideWarningBanner() {
      this.showWarningBanner= false;
  }

  public discardChanges() {
    this.store.dispatch(clearBookingFormData());
    this.closeCallback.emit(true)
  }
}
